export default {
	data() {
		return {
			xuan1: false,
			xuan2: false,
			xuan3: false,
			tatol: '0',
			disabled: false,
		};
	},
	methods: {
		xuan(x) {
			if (x == 1) {
				this.xuan2 = false;
				this.xuan3 = false;
				if (this.xuan1 == false) {
					this.xuan1 = true;
					this.tatol=299
				} else {
					this.xuan1 = false;
					this.tatol=0
				}
			}
			if (x == 2) {
				this.xuan1 = false;
				this.xuan3 = false;
				if (this.xuan2 == false) {
					this.xuan2 = true;
					this.tatol=299
				} else {
					this.xuan2 = false;
					this.tatol=0
				}
			}
			if (x == 3) {
				this.xuan2 = false;
				this.xuan1 = false;
				if (this.xuan3 == false) {
					this.xuan3 = true;
					this.tatol=299
				} else {
					this.xuan3 = false;
					this.tatol=0
				}
			}
		},
		submit() {
			let _this = this;
			uni.showLoading({
				title: '提交中'
			});
			leanCloud.Cloud.run('wxOrder', { productDescription: '佰橙科技-装修监理费', amount: _this.tatol*100}).then(data => {
					console.log(data);
					data.success = () => {
						// 支付成功
						//uni.setStorageSync('orderId', uni.getStorageSync('orderId') + 1);
						var Todo = leanCloud.Object.extend('Order');
						var todo = new Todo();
						todo.set('user', leanCloud.User.current());
						todo.set('city', _this.city);
						todo.set('name', _this.name);
						todo.set('mobilePhoneNumber', _this.phone);
						todo.set('area', parseFloat(_this.area));
						todo.set('status', '已完成');
						todo.set('total', _this.tatol);
						todo.set('serviceType', '全程监理服务');
						todo.set('servicePrice', 29);
						todo.set('address', _this.aress);
						todo.set('serviceId', 2);
						todo.set('insuranceType', uni.getStorageSync('insuranceType'));
						todo.set('insurancePrice', uni.getStorageSync('safe'));
						todo.set('insuranceId', uni.getStorageSync('insuranceId'));
						//todo.set('createdAt', new Date().toLocaleString().replace('上午','').replace('下午',''));
						//todo.set('updatedAt', new Date().toLocaleString().replace('上午','').replace('下午',''));
						//todo.set('orderId', uni.getStorageSync('orderId')+1);
						todo.save().then(
							function(todo) {
								uni.hideLoading();
								uni.showToast({
									title: '支付成功',
									duration: 1000
								});
								setTimeout(function() {
									uni.navigateTo({
										url: '../index/index'
									});
								}, 1000);
							},
							function(error) {
								uni.hideLoading();
								console.log(error)
								/* uni.navigateTo({
									url: '../submit-fail/submit-fail'
								}); */
							}
						);
					};
					data.fail = ({ errMsg }) => {
						console.log(errMsg)
						uni.showToast({
							title: "支付失败",
							duration: 1000
						});
					};
					uni.requestPayment(data);
				}).catch(error => {
					console.log(error);
				});
		}
	},
	updated() {
		if ( (this.xuan1 != false || this.xuan2 != false || this.xuan3 != false)) {
			this.disabled = true;
		} else {
			this.disabled = false;
		}
	},
	created() {
	}
};
